	module.exports = {"data":{"challengeNode":{"title":"Size Your Images","description":["CSS 的<code>width</code>属性可以控制元素的宽度。图片的<code>width</code>宽度类似于字体的<code>px</code>（像素)值。","假如，你想创建一个叫<code>larger-image</code>的 CSS class 来控制 HTML 元素的宽度为 500px，我们可以这样做：","<blockquote>&#60;style&#62;<br>&nbsp;&nbsp;.larger-image {<br>&nbsp;&nbsp;&nbsp;&nbsp;width: 500px;<br>&nbsp;&nbsp;}<br>&#60;/style&#62;</blockquote>","<hr>","创建一个<code>smaller-image</code>的 CSS class，设置图片的宽度为 100px。","<strong>注意：</strong><br>由于不同浏览器的差异性，你可能需要将浏览器缩放到 100% 来通过该挑战。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/basic-css/size-your-images","blockName":"Basic CSS","tests":[{"text":"<code>img</code>元素应该含有<code>smaller-image</code> class。","testString":"assert($(\"img[src='http://cdn.freecodecamp.cn/relaxing-cat.jpg']\").attr('class') === \"smaller-image\", '<code>img</code>元素应该含有<code>smaller-image</code> class。');"},{"text":"图片宽度应为 100px（像素），且浏览器缩放应为默认 100%。","testString":"assert($(\"img\").width() === 100, '你的图片应为 100px（像素），且浏览器缩放应为默认 100%。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<link href=\"https://fonts.googleapis.com/css?family=Lobster\" rel=\"stylesheet\" type=\"text/css\">\n<style>\n  .red-text {\n    color: red;\n  }\n\n  h2 {\n    font-family: Lobster, monospace;\n  }\n\n  p {\n    font-size: 16px;\n    font-family: monospace;\n  }\n</style>\n\n<h2 class=\"red-text\">CatPhotoApp</h2>\n<main>\n  \n  <a href=\"#\"><img src=\"http://cdn.freecodecamp.cn/relaxing-cat.jpg\" alt=\"一只仰卧着的萌猫\"></a>\n  \n  <p>猫咪最喜欢的三件东西：</p>\n  <ul>\n    <li>猫薄荷</li>\n    <li>激光笔</li>\n    <li>千层饼</li>\n  </ul>\n  <p>猫咪最讨厌的三件东西：</p>\n  <ol>\n    <li>跳蚤</li>\n    <li>打雷</li>\n    <li>同类</li>\n  </ol>\n  \n  <form action=\"/submit-cat-photo\">\n    <label><input type=\"radio\" name=\"indoor-outdoor\">室内</label>\n    <label><input type=\"radio\" name=\"indoor-outdoor\">室外</label><br>\n    <label><input type=\"checkbox\" name=\"personality\">忠诚</label>\n    <label><input type=\"checkbox\" name=\"personality\">懒惰</label>\n    <label><input type=\"checkbox\" name=\"personality\">积极</label><br>\n    <input type=\"text\" placeholder=\"猫咪图片地址\" required>\n    <button type=\"submit\">提交</button>\n  </form>\n</main>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/basic-css/add-borders-around-your-elements","id":"bad87fee1348bd9acdf08812"},"slug":"/responsive-web-design/basic-css/size-your-images"}}

